<template>
    <div class="panel panel-default">
        <div class="panel-heading">
            <h4>
                <router-link to="/" class="pull-left"></router-link>
                HdSmart.Device.postRequest
            </h4>
        </div>
        <div class="panel-body">
            <pre class="text-left">
//appHdIot.Device.postRequest
HdSmart.Device.postRequest('getDeviceLog',{
    page_number:1,
    items_per_page:10,
    start_time: new Date(2017,5,10),
},function(data){
    this.messages.push('=>' + JSON.stringify(data));
}, function(error){
    this.messages.push('=>' + JSON.stringify(error));
})
            </pre>
            <div v-for="msg in messages">
                {{msg}}
            </div>
        </div>
        <div class="panel-footer">
            <button class="btn btn-default" @click="onClick"></button>
        </div>

    </div>
</template>
<script>
    export default {
        name: "getSnapShot",
        data (){
            return {
                page_number: 1,
                messages: [" ..."]
            }
        },
        methods: {
            onClick (){
                var _this = this;
                HdSmart.Device.postRequest('getDeviceLog',{
                    page_number: this.page_number,
                    items_per_page: 10,
                    start_time: +new Date(2017,5,10)
                }, function (data) {
                    _this.page_number = _this.page_number + 1;
                    _this.messages.push('=>' + JSON.stringify(data));
                }, function (error) {
                    _this.messages.push('=>' + JSON.stringify(error));
                })
            }
        }
    }
</script>


