<template>
    <div class="panel panel-default">
        <div class="panel-heading">
            <h4>
                <router-link to="/" class="pull-left"></router-link>
                HdSmart.Device.getDeviceLogByDay
            </h4>
        </div>
        <div class="panel-body">
            <pre class="text-left">
//appHdIot.Device.getDeviceLogByDay
HdSmart.Device.getDeviceLog(+new Date('2017-05-03 05:00:00'),function(data){
    this.messages.push('=>' + JSON.stringify(data));
}, function(error){
    this.messages.push('=>' + JSON.stringify(error));
})
            </pre>
            <div v-for="msg in messages">
                {{msg}}
            </div>
        </div>
        <div class="panel-footer">
            <button class="btn btn-default" @click="onClick"></button>
        </div>

    </div>
</template>
<script>
    export default {
        name: "getSnapShot",
        data (){
            return {
                messages: [" ..."]
            }
        },
        methods: {
            onClick (){
                var _this = this;
                HdSmart.Device.getDeviceLogByDay(+new Date('2017-05-03 05:00:00'), function (data) {
                    _this.messages.push('=>' + JSON.stringify(data));
                }, function (error) {
                    _this.messages.push('=>' + JSON.stringify(error));
                })
            }
        }
    }
</script>


