<template>
    <div class="panel panel-default">
        <div class="panel-heading">
            <h4>
                <router-link to="/" class="pull-left"></router-link>
                HdSmart.Device.control
            </h4>
        </div>
        <div class="panel-body">
            <pre class="text-left">
//appHdIot.Device.control
            </pre>
            <div v-for="msg in messages">
                {{msg}}
            </div>
        </div>
        <div class="panel-footer">
            <button class="btn btn-default" @click="onOpen"></button>
            <button class="btn btn-default" @click="onPause"></button>
            <button class="btn btn-default" @click="onClose"></button>
        </div>

    </div>
</template>
<script>
    export default {
        name: "ready",
        data (){
            return {
                messages: [" ..."]
            }
        },
        methods: {
            onOpen (){
                HdSmart.Device.control('setZigbeeCurtain', 'setOnoff', {
                    mode: 'on'
                }, (data) => {
                    this.messages.push('open=>' + JSON.stringify(data));
                }, (error) => {
                    this.messages.push('open=>' + JSON.stringify(error));
                })
            },

            onPause (){
                HdSmart.Device.control('setZigbeeCurtain', 'setOnoff', {
                    mode: 'pause'
                }, (data) => {
                    this.messages.push('pause=>' + JSON.stringify(data));
                }, (error) => {
                    this.messages.push('pause=>' + JSON.stringify(error));
                })
            },

            onClose (){
                HdSmart.Device.control('setZigbeeCurtain', 'setOnoff', {
                    mode: 'off'
                }, (data) => {
                    this.messages.push('off=>' + JSON.stringify(data));
                }, (error) => {
                    this.messages.push('off=>' + JSON.stringify(error));
                })
            }
        }
    }
</script>


