<template>
    <div class="panel panel-default">
        <div class="panel-heading">
            <h4>
                <router-link to="/" class="pull-left"></router-link>
                HdSmart.Util.sendNotification
            </h4>
        </div>
        <div class="panel-body">
            <pre class="text-left">
//appHdIot.Util.sendNotification
HdSmart.Util.sendNotification('notification','notification')
            </pre>
            <div v-for="msg in messages">
                {{msg}}
            </div>
        </div>
        <div class="panel-footer">
            <button class="btn btn-default" @click="onClick"></button>
        </div>

    </div>
</template>
<script>
    export default {
        name: "ready",
        data (){
            return {
                messages: [" ..."]
            }
        },
        methods: {
            onClick (){
                HdSmart.Util.sendNotification('notification', 'notification')
                this.messages.push('notification');
            }
        }
    }
</script>


